@charset "utf-8";

header {
    background-color: rgb(65, 65, 255);
    text-align: center;
    margin: 0;
    padding: 10px;
}

body {
    margin: 0;
}

#main-wrap{
    height: 100svh;
    /* height: 100dvh; */

} 


.content-wrap{
    width: 90%;
    margin: auto;
}


#timer-display {
    width: 60px; /* タイマー表示のサイズを調整 */
    height: 60px;
    line-height: 60px; /* 垂直方向の中央揃えのための設定 */
    text-align: center; /* 水平方向の中央揃えのための設定 */
    font-size: 1.6em;
    color: #fff;
    background-color: #333;
    border-radius: 50%; /* 50%に設定して円形にします */
    display: inline-block;
    animation: countdownAnimation 1s linear infinite;
    position: absolute;
    right: 10px;
}

@keyframes countdownAnimation {
    0% {
        background-color: #ff6347; /* 開始時の色に変更 */
        transform: scale(1);
    }
    50% {
        background-color: #ffa07a; /* 途中の色に変更 */
        transform: scale(1.1);
    }
    100% {
        background-color: #ff6347; /* 終了時の色に変更 */
        transform: scale(1);
    }
}


h1 {
    margin: 0;
    font-size: 1.4em;
    color: white;
    padding: 0px;
}

h2 {
    margin: 0;
    font-size: 1.4em;

}



.top-display #points {
    font-size: 2em;
    text-align: center;
}

.center {
    text-align: center;
}


#video {
    width: 100%;
    height: 100%;
    z-index: 50;
    margin: 0 auto;
    vertical-align: bottom;
}


#color-display {
    z-index: 10;
    padding: 10px;
    height: 400px;
}

p {
    margin: 0;
}

.hint {
    font-size: 1.1em;
    margin: auto;
}

#iro {
    text-align: center;
    font-size: 2em;
    margin: 3% 36%;
}




#checkColorTypeMatch {
    line-height: 50px; /* 垂直方向の中央揃えのための設定 */
    height: 50px;
    width: 60%;
    display: inline-block;
    padding: 0.5em 1em;
    text-decoration: none;
    background: #668ad8;
    /*ボタン色*/
    color: white;
    /*ボタン色と同じに*/
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.29);
    border-bottom: solid 3px #627295;
    border-radius: 30px;
    /*角の丸み*/
    font-weight: bold;
    text-shadow: -1px -1px rgba(255, 255, 255, 0.44), 1px 1px rgba(0, 0, 0, 0.38);
}

#checkColorTypeMatch:active {
    /*ボタンを押したとき*/
    -webkit-transform: translateY(4px);
    transform: translateY(4px);
    box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.2);
    border-bottom: none;
}




